form {
  flex-direction: column;
  align-items: center;
  margin-top: 50px;
  display: flex;
}

label {
  flex-direction: column;
  margin-top: 10px;
  display: flex;
}

button[type="submit"] {
  cursor: pointer;
  border-radius: 4px;
  margin-top: 10px;
  padding: 2px 7px;
}

button[type="submit"]:hover {
  background-color: #adff2f;
}

a {
  color: #333;
  max-width: 150px;
  background-color: rgba(0, 0, 0, 0);
  border-radius: 320px;
  margin-bottom: 20px;
  padding: 8px 12px;
  font-size: 16px;
  font-weight: bold;
  text-decoration: none;
}

a:hover {
  color: #fff;
  background-color: #000;
}

